@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?1fubez');
  src:  url('../fonts/icomoon.eot?1fubez#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?1fubez') format('truetype'),
    url('../fonts/icomoon.woff?1fubez') format('woff'),
    url('../fonts/icomoon.svg?1fubez#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-visualstudiocode:before {
  content: "\e903";
}
.icon-bootstrap:before {
  content: "\e902";
}
.icon-typescript:before {
  content: "\e901";
}
.icon-mysql:before {
  content: "\e900";
}
